}

label {
  display: table-cell;
  vertical-align: middle;
  cursor: pointer;
  background-color: white;
  border: 3px solid;
  border-color: #205c9c;
  color: #205c9c;
  padding: 5px 10px;
  border-radius: 20px;
}

input[type="radio"]:checked + label {
  background-color: #205c9c;
  color: white;
}

